import React, { useState } from 'react';
import './SubForm.css';

const HateLove = ({modifyIndex}) => {

    const [formData, setFormData] = useState({
        prefs: {
            love: "",
            hate: ""
        }
    })


    const handleTxtArea = (e, pref) => {
        if(pref === 'love'){
            setFormData({
                prefs: {
                    ...formData.prefs,
                    love: e.target.value
                }
            })
        } else if(pref === 'hate'){
            setFormData({
                prefs: {
                    ...formData.prefs,
                    hate: e.target.value
                }
            })
        }
    }


    const preventFunc = e => {
        e.preventDefault()

    };

    return (
        <form 
        onSubmit={preventFunc}
        className="preferences-form">
            <p>Parle nous des aliments que tu pr√©f√®res et que tu deteste !</p>

            <label htmlFor="prefered">Tes aliments pr√©f√©r√©s, s√©par√©s par une virgule :</label>
            <textarea onChange={e => handleTxtArea(e, "love")} id="prefered" placeholder='Un ou plusieurs, si tu en as.'></textarea>

            <label htmlFor="hated">Les aliments que tu ne supporte pas, s√©par√©s par une virgule</label>
            <textarea onChange={e => handleTxtArea(e, "hate")} id="hated" placeholder='Un ou plusieurs, si tu en as.'></textarea>


            <div className="container-nav-btns">
                <button type="button" onClick={() => modifyIndex(4)}>Pr√©c√©dent</button>
                <button onClick={() => modifyIndex(6, formData)}>Valider</button>
            </div>
        </form>
    );
};

export default HateLove;