import React, { useState } from 'react';
import './SubForm.css';


const DietForm = ({modifyIndex}) => {

    const [formData, setFormData] = useState({
        dietForm: 'nodiet'
    })

    const handleRadio = e => {
        setFormData({
            dietForm: e.target.value
        })
    }

    const preventFunc = e => e.preventDefault();

    return (
        <form 
        onSubmit={preventFunc}
        className='diet-form'>
            <p>Quelle est ton r√©gime alimentaire ?</p>

            <label htmlFor="nodiet">Pas de r√©gime en particulier</label>
            <input 
            onChange={handleRadio}    
            type="radio"
            name="diet"
            id="nodiet"
            value="nodiet"
            />

            <label htmlFor="homnivorous">Homnivore</label>
            <input 
            onChange={handleRadio}    
            type="radio"
            name="diet"
            id="homnivorous"
            value="homnivorous"
            />

            <label htmlFor="vegetarian">V√©g√©tarien</label>
            <input 
            onChange={handleRadio}    
            type="radio"
            name="diet"
            id="vegetarian"
            value="vegetarian"
            />

            <label htmlFor="vegan">Vegan</label>
            <input 
            onChange={handleRadio}    
            type="radio"
            name="diet"
            id="vegan"
            value="vegan"
            />

            <button onClick={() => modifyIndex(3, formData)}>Valider</button>
        </form>
    );
};

export default DietForm;