import React, { useRef } from 'react';
import './SubForm.css';

const FoodStyle = ({modifyIndex}) => {

    const preventFunc = (e) => {
        e.preventDefault()

        const styleData = {
            foodStyle : []
        }

        allCheckboxes.current.forEach(checkbox => {
            if(checkbox.checked){
                styleData.foodStyle.push(checkbox.value)
            }
        })

        modifyIndex(4, styleData);

    };

    const allCheckboxes = useRef([]);

    const addCheck = el => {
        if(el && !allCheckboxes.current.includes(el)){
            allCheckboxes.current.push(el);
        }
    }

    return (
        <form
        onSubmit={preventFunc}
        className="checkbox-form">
            
            <p>Quelles sont tes cuisines pr√©f√©es ?</p>
            <span>Choix multiples autoris√©s.</span>


            <label htmlFor="italian">Italienne</label>
            <input ref={addCheck} type="checkbox" id="italian" value="italienne" />

            <label htmlFor="japanese">Japonaise</label>
            <input ref={addCheck} type="checkbox" id="japanese" value="japanese" />

            <label htmlFor="indian">Indienne</label>
            <input ref={addCheck} type="checkbox" id="indian" value="indian" />

            <label htmlFor="thai">Tha√Ølandaise</label>
            <input ref={addCheck} type="checkbox" id="thai" value="thai" />

            <label htmlFor="french">Fran√ßaise</label>
            <input ref={addCheck} type="checkbox" id="french" value="french" />

            <label htmlFor="chinese">Chinoise</label>
            <input ref={addCheck} type="checkbox" id="chinese" value="chinese" />

            <div className="container-nav-btns">
                <button onClick={() => modifyIndex(2)} type="button" className='prev'>Pr√©c√©dent</button>
                <button>Valider</button>
            </div>
        </form>
    );
};

export default FoodStyle;